import React from 'react';

import P5Wrapper from 'react-p5-wrapper';
import sketch from '../projects/Matrix/sketch';

import Matrix2 from '../projects/Matrix2/Matrix2';
import Matrix from '../projects/Matrix/Matrix';

import '../Home/home.css';
class Home extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            resized: false,
            width: window.innerWidth
        }
        window.addEventListener('resize', () => {
            console.log(this.state.id)

            if (window.innerWidth != this.state.width) {
                this.setState({
                    resized: true,
                    width: window.innerWidth
                })
            }
        });
        localStorage.clear()
    }

    resized() {
        if (this.state.resized) {
            return <div id="resize">
                <h1>Please Refresh Page</h1>
                <p>This allows the p5.js canvases to correctly resize</p>
            </div>
        }
    }

    intro() {
        return <div id="intro">
            <h1>Kieran Brett</h1>
        </div>
    }


    render() {
        return <div id="home">
            {this.intro()}

            <Matrix />
            <Matrix2 /> 
            {this.resized()}
        </div>
    }
}

export default Home;
